import React from 'react';
import Box from '@material-ui/core/Box'
import { ILog } from '../../interfaces/ILog';
import { Log } from '../../components/atoms/log/Log';

export function Logs(props: { logs: ILog[] }) {
    const { logs } = props
    const formatLogs = (logs: ILog[]): ILog[][] => {
        if (logs.length === 0) return [[]];

        const ret: ILog[][] = [[]]
        let i = 0
        let prev = new Date(logs[0].start).getHours()
        logs.forEach((log: ILog) => {
            const start = new Date(log.start).getHours()
            if (prev === start) {
                ret[i].push(log)
            } else if (ret[i].length === 0) {
                ret[i].push(log)
                prev = start
            } else {
                i++
                ret[i] = []
                prev = start
            }
        })
        return ret
    }
    const formatDate = (logs: ILog[]): string => {
        if (logs.length === 0) return '';
        const startHour: number = new Date(logs[0].start).getHours()
        const endHour: number = new Date(logs.slice(-1)[0].end).getHours()
        return `# ${startHour}:00 - ${endHour + 1}:00`;
    }
    return (
        <Box display="flex" flexDirection="column">
            {formatLogs(logs).reverse().map(logs =>
                <div>
                    <div>{formatDate(logs)}</div>
                    {logs.reverse().map(log => <Log key={log.taskName} taskName={log.taskName} />)}
                </div>)}
        </Box>
    );
}
